<template>
    <Header :homeDisplay="this.homeDisplay"/>
    <h3> [ 일자별 식수확인 ] </h3>
    <div class="tablepick">
        <div class="mycal">
            <div class="justify-content-center">
                <VDatePicker :attributes='attributes' @dayclick="onDayClick" mode="multiple" 
                :disabled-dates="disabledDates" :color="selectedColor" :min-date='minDate' 
                :disabled-attribute='disabledAttribute' expanded class="my-calendar" />
            </div>
        </div>

        
    </div>
    <div class="mb-3 border bg-warning border-warning rounded p-2" style="--bs-bg-opacity: 0.2" v-if="!!this.pick_date_s">
            [ {{ this.pick_date_s }} ] 식수 인원 : <span class="eat">{{this.ticketLength}}</span> / 전체 {{usersLength}} 명
    </div>    
    <!-- <div class="total"> 
        <h3>식 수 : <span>{{this.ticketLength}}</span> /전체 : {{usersLength}} 명</h3>
    </div>     -->
</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";

var holidays= ref([]);
const selectedColor = ref('yellow');
const users=ref([]);
const in_ticketLength = ref(0);

const disabledDates = ref([{
        repeat: {
            weekdays: [1, 7],
        },
}]);

const disabledAttribute = ref([{
        contentStyle: {
            color: 'red',
        },
        contentHoverStyle: {
                cursor: 'default',
                backgroundColor: 'transparent',
        },
}]);

const homeDisplay = ref(true);
const minDate= ref(new Date());

export default {
    name: "DayTicketView",
    components: { 
        Header,
    },    
    data() {
            return {
                holidays: holidays,
                date: new Date(),
                disabledDates,
                selectedColor,
                disabledAttribute,
                message:"",
                timer: 1,
                interval: "",
                homeDisplay: homeDisplay,
                users: users,
                ticketLength: in_ticketLength,
                usersLength:0,
                pick_date_s:'',
                minDate:minDate,                
            }
    },
    computed: {        
        fetchHolidayList() {
            return this.$store.state.holiday.holidayList;            
        },        
        holidates() {
            return this.holidays.map(day => day.date);
        },
        attributes() {
            return [
                {
                    contentStyle: {
                        fontWeight: '700',
                        fontSize: '.9rem',
                    },
                    dates: new Date(),
                },                
                ...this.holidates.map(date => ({
                    highlight: 'red',
                    dates: date,
                })),
            ];
        },
        getAllTicketList() {        
            return this.$store.state.tickets.allticketList;            
        },
        getAlluserList() {
            return this.$store.state.auth.userList;
        },         
    },
    watch:{
        fetchHolidayList(val){
            if (val.length>0) {
                this.holidays = val;            
                let tdays=[];
                val.forEach ( (day) => {
                    tdays.push(day);
                })
                this.twin_holidays = tdays;
            }            
        },
        getAllTicketList(val){
            if ( val.length > 0) {
                let usersArray=[];
                let users = this.$store.state.auth.userList;
                this.usersLength =users.length;
                if (users.length<0) {
                    this.$store.dispatch("fetchAlluserList");
                    users = this.$store.state.auth.userList;
                }
                if (users.length>0) {
                    val.forEach( (ticket) =>{
                        var hasname = e => e.uid === ticket.uid;
                        let uname= users.find(hasname);
                        if( uname != null){
                            usersArray.push(uname);
                        }                        
                    });
                    this.users = usersArray;                    
                    this.ticketLength = this.users.length;
                }                
            }
        },
        getAlluserList(val) {
            this.userList = val;         
        }
    },
    mounted() {
        this.$store.dispatch("fetchAlluserList");        
        this.$store.dispatch("fetchHolidayList");
        this.check_time_over(); 
    },    
    methods: {            
        onDayClick(day) {

            const now = new Date();
            let overindex=false;                
            const offset = new Date().getTimezoneOffset() * 60000
            const date = new Date(day.date - offset);                
            let now_t = now.getFullYear()+'-'+ (now.getMonth()+1)+ '-'+now.getDate();                
            let lapstime = new Date(now_t);
            lapstime.setSeconds(lapstime.getSeconds() + 33300);    //09:15
            // lapstime.setSeconds(lapstime.getSeconds() + 34200);    //09:30
            // lapstime.setSeconds(lapstime.getSeconds() + 32400); //09:00
            // this.minDate = now.setDate(now.getDate()+1);
            if (now >= lapstime){
                overindex=true;
            }

            if( date > lapstime || !overindex ) {
                const idx = this.holidays.findIndex(d => d.id === day.id);
                if (idx < 0) {            
                    const mydate = new Date(day.id);
                    let weekend = this.dateDisabled(mydate);                    
                    if (!weekend) {
                        this.pick_date_s='';
                        this.pick_date_s = date.getFullYear()+'년  '+date.getMonth()+'월  '+date.getDate()+'일';
                        this.ticketLength=0;
                        this.usersLength=0;
                        this.$store.dispatch("fetchAllTicketList", day.id); 
                    } else {
                        this.pick_date_s='';
                    }
                } else {
                    this.pick_date_s='';
                }
            }

            // const offset = new Date().getTimezoneOffset() * 60000
            // const date = new Date(day.date - offset);
            // let dayid = date.toISOString().split('T')[0];
            
        },
            
        dateDisabled(date) {                
            const weekday = date.getDay()
            return weekday === 0 || weekday === 6
        },

        check_time_over() {
                let now = new Date();
                let now_t = now.getFullYear()+'-'+ (now.getMonth()+1)+ '-'+now.getDate();                
                let lapstime =new Date(now_t);
                lapstime.setSeconds(lapstime.getSeconds() + 33300);    //09:15
                // lapstime.setSeconds(lapstime.getSeconds() + 34200);    //09:30
                // lapstime.setSeconds(lapstime.getSeconds() + 32400); //09:00
                if (now >= lapstime){
                    this.minDate = now.setDate(now.getDate()+1);
                    // console.log('time over', this.minDate);       
                } else {
                    this.minDate = now;
                    // console.log('time less', this.minDate);       
                }
        },
    },
    
};
</script>

<style scoped>
.my-calendar .vc-weekday-1, .my-calendar .vc-weekday-7 {
    color: #f50303;
}

.my-calendar.vc-container {
    --day-border: 1px solid #b8c2cc;
    --day-border-highlight: 1px solid #b8c2cc;
    --day-width: 90px;
    --day-height: 90px;
    --weekday-bg: #f8fafc;
    --weekday-border: 1px solid #eaeaea;

    border-radius: 5%;
    width: 98%;
}
.tablepick {
    padding: 4%;
}
.mycal{
    padding: 10px;
}
.footer{
    padding: 20px;
}
.eat{
    color:blue;
}
h3 {
    text-align: center;
}

</style>